import { vec2 } from "../utils/matrix"

export enum RoomType {
    Exterior,
    PublicCourtyard,
    PublicRoom,
    PrivateCourtyard,
    PrivateRoom,
    Vault,
    Bedroom,
    Dining,
    PublicLibrary,
    PrivateLibrary,
    Kitchen,
    Treasure,
    TreasureCourtyard,
    LockedTreasure,
    LockedTreasureCourtyard,
    ThroneRoom,
}

export enum DoorType {
    Standard,
    GateFront,
    GateBack,
    Locked,
}

export type Room = {
    roomType: RoomType,
    group: number,
    depth: number,
    betweenness: number,
    privateRoom: boolean,
    posMin: vec2,
    posMax: vec2,
    edges: Array<Adjacency>,
    gridX: number,
    gridY: number,
}

export type Adjacency = {
    origin: vec2,
    dir: vec2,
    length: number,
    roomLeft: Room,
    roomRight: Room,
    nextMatching: Adjacency | null,
    door: boolean,
    doorType: DoorType,
}

export type PatrolRoute = {
    path: Array<vec2>,
    minRoomDepth: number,
    maxRoomDepth: number,
}

export type PatrolNode = {
    room: Room;
    nodeNext: PatrolNode | null;
    nodePrev: PatrolNode | null;
    visited: boolean;
}

export type Visit = {
    stackLen: number;
    room: Room;
}