import { RNG } from "./random";

export function removeByValue<T>(array: Array<T>, value: T) {
    const i = array.indexOf(value);
    array.splice(i, 1);
}

export function randomSetMember<T>(items: Set<T>, rng: RNG): T | undefined {
    const itemsAsList = Array.from(items);
    if (itemsAsList.length === 0) {
        return undefined;
    }

    return itemsAsList[rng.randomInRange(itemsAsList.length)];
}