import { GameMap, itemLayers, ItemType } from "../../game-map";
import { vec2 } from "../../utils/matrix";
import { RNG } from "../../utils/random";
import { doorAdjacent, windowAdjacent } from "./adjacent";


export function placeItem(map: GameMap, pos: vec2, type: ItemType) {
    map.items.push({
        pos: vec2.clone(pos),
        type: type,
        topLayer: itemLayers[type],
    });
}

export function tryPlaceItem(map: GameMap, pos: vec2, itemType: ItemType) {
    if (doorAdjacent(map.cells, pos)) {
        return;
    }

    if ((itemType == ItemType.TorchUnlit || itemType == ItemType.TorchLit) &&
        windowAdjacent(map.cells, pos)) {
        return;
    }

    placeItem(map, pos, itemType);
}

export function randomlyLitTorch(level: number, rng: RNG): ItemType {
    if (level === 0) {
        return ItemType.TorchUnlit;
    }

    return (rng.random() < 0.05) ? ItemType.TorchUnlit : ItemType.TorchLit;
}

export function isTallItemType(itemType: ItemType): boolean {
    switch (itemType) {
        case ItemType.Bookshelf:
        case ItemType.DrawersShort:
        case ItemType.VaultTreasureBox:
        case ItemType.EmptyVaultTreasureBox:
        case ItemType.LootedVaultTreasureBox:
        case ItemType.DrawersTall:
        case ItemType.Shelf:
        case ItemType.TorchUnlit:
        case ItemType.TorchLit:
        case ItemType.Stove:
            return true;
        default:
            return false;
    }
}

export function pillarRoomEndFurniture(level: number, rng: RNG): ItemType {
    const u = rng.random();
    if (u < 0.25) {
        return ItemType.Table;
    } else {
        return randomlyLitTorch(level, rng);
    }
}

export function isDoorItemType(itemType: ItemType): boolean {
    return itemType >= ItemType.DoorNS && itemType <= ItemType.PortcullisEW;
}

export function blocksPlayerMovement(itemType: ItemType): boolean {
    return itemType === ItemType.DrawersTall ||
           itemType === ItemType.Bookshelf ||
           itemType === ItemType.Shelf ||
           itemType === ItemType.Stove ||
           itemType === ItemType.TreasureLock;
}

export function canHoldLoot(itemType: ItemType): boolean {
    switch (itemType) {
        case ItemType.Chair:
        case ItemType.Table:
        case ItemType.DrawersShort:
        case ItemType.DrawersTall:
        case ItemType.Bookshelf:
        case ItemType.Shelf:
        case ItemType.Bush:
            return true;
        default:
            return false;
    }
}

export function canHoldHealth(itemType: ItemType): boolean {
    return itemType === ItemType.Table;
}